import styles from '../styles/mainImg.module.scss'
import React, { useRef } from 'react'
import { Canvas, useFrame, useLoader } from '@react-three/fiber'
import {
  useGLTF,
  Stage,
  PresentationControls,
  OrbitControls,
} from '@react-three/drei'
import { GLTFLoader } from 'three/addons/loaders/GLTFLoader.js'
import asset from '../assets/Heart.gltf'

function Mesh() {
  const mesh = useRef(null)
  useFrame(() => (mesh.current.rotation.y = mesh.current.rotation.z += 0.01))
  return (
    <mesh ref={mesh} scale={1}>
      <ambientLight intensity={1} />
      <directionalLight position={[-1, 0, 1]} intensity={0.5} />
      <cylinderGeometry args={[1, 1, 0.3, 50]} />
      <meshStandardMaterial attach="material" color={0xa3b18a} />
    </mesh>
  )
}

function Model() {
  const gltf = useLoader(GLTFLoader, asset)
  return <primitive object={gltf.scene} scale={0.5} />
}

function MainImg() {
  return (
    <div className={styles.mainImg}>
      <Canvas camera={{ fov: 80, near: 0.1, far: 100, position: [0, 20, 0] }}>
        <Stage environment={'sunset'} shadows={false}>
          <OrbitControls autoRotate={true} minDistance={1} />
          {/* <ambientLight intensity={1} flatShading={false} /> */}
          <directionalLight position={[-1, 0, 1]} intensity={0.5} />
          <Model />
        </Stage>
      </Canvas>
    </div>
  )
}

export default MainImg
